.editor-styles-wrapper .wp-block {
  border: 1px dashed #999;
  color: #F7F7F7;
}

.wp-block {
  max-width: 840px;
}

.wp-block[data-align=wide] {
  max-width: 1080px;
}

.wp-block[data-align=full] {
  max-width: none;
}

.rich-text.block-editor-rich-text__editable.wp-block-button__link:visited {
  border-radius: 0;
  color: #00AEC3;
  border-color: #00AEC3;
  border: 1px solid;
  background-color: white;
}